"use client";
import React, { useState, useEffect, useRef } from "react";
import Header from "@/components/Header";

// TriCoin Hash Algorithm
const PHI = 1.61803398875;
const DEFAULT_SALT_LENGTH = 153;

function generateSalt(length = DEFAULT_SALT_LENGTH) {
  const crypto = window.crypto || (window as any).msCrypto;
  const array = new Uint32Array(Math.ceil(length / 32));
  crypto.getRandomValues(array);
  return Array.from(array, (dec) =>
    ("0".repeat(32) + dec.toString(2)).slice(-32)
  )
    .join("")
    .slice(0, length);
}

function phase1(input: string, customSalt = "") {
  const blockSize = Math.floor(input.length / (PHI * PHI));
  const salt = customSalt || generateSalt();
  const paddedInput = input + salt;

  return [
    paddedInput.slice(0, blockSize),
    paddedInput.slice(blockSize, 2 * blockSize),
    paddedInput.slice(2 * blockSize),
  ];
}

function goldenShift(text: string, shiftValue: number) {
  return text
    .split("")
    .map((c, i) => {
      const hash = Math.floor(i * shiftValue * 1000) % 65536;
      return String.fromCharCode((c.charCodeAt(0) + hash) % 65536);
    })
    .join("");
}

function xorWithPattern(str: string, pattern = "1010101") {
  return str
    .split("")
    .map((c, i) =>
      String.fromCharCode(
        c.charCodeAt(0) ^ pattern.charCodeAt(i % pattern.length)
      )
    )
    .join("");
}

function holyPermutation(str: string, pattern = [3, 7, 12]) {
  return pattern.map((p) => str[p % str.length] || str[0]).join("");
}

function phase2(blocks: string[]) {
  return blocks.map((block) => {
    const step1 = goldenShift(block, PHI);
    const step2 = xorWithPattern(step1);
    return holyPermutation(step2);
  });
}

function phase3(blocks: string[]) {
  let mixed = blocks.join("");

  for (let i = 0; i < 40; i++) {
    mixed = mixed
      .split("")
      .map((c, j) => {
        const prev = j > 0 ? mixed.charCodeAt(j - 1) : 0;
        const next = j < mixed.length - 1 ? mixed.charCodeAt(j + 1) : 0;
        return String.fromCharCode(
          (c.charCodeAt(0) + prev + next + Math.floor(PHI * 10000 * i)) % 65536
        );
      })
      .join("");
  }

  return mixed.slice(0, 1613);
}

function ts1613Hash(input: string, salt = "", iterations = 1000) {
  let blocks = phase1(input, salt);
  let processedBlocks = phase2(blocks);
  let hash = phase3(processedBlocks);

  for (let i = 0; i < iterations; i++) {
    blocks = phase1(hash, salt + i);
    processedBlocks = phase2(blocks);
    hash = phase3(processedBlocks);
  }
  return hash;
}

export default function MinerPage() {
  const [isMining, setIsMining] = useState(false);
  const [hashRate, setHashRate] = useState(0);
  const [totalHashes, setTotalHashes] = useState(0);
  const [minedCoins, setMinedCoins] = useState(0);
  const [currentHash, setCurrentHash] = useState("");
  const [timeToListing, setTimeToListing] = useState("");
  const [listingDate, setListingDate] = useState("");
  const [difficulty, setDifficulty] = useState(4);
  
  const miningIntervalRef = useRef<NodeJS.Timeout | null>(null);
  const hashRateIntervalRef = useRef<NodeJS.Timeout | null>(null);
  const hashCountRef = useRef(0);

  // Set listing date (you can modify this)
  const LISTING_DATE = new Date("2024-12-31T23:59:59");

  useEffect(() => {
    const timer = setInterval(() => {
      const now = new Date();
      const timeDiff = LISTING_DATE.getTime() - now.getTime();
      
      if (timeDiff > 0) {
        const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));
        const hours = Math.floor((timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((timeDiff % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((timeDiff % (1000 * 60)) / 1000);
        
        setTimeToListing(`${days}d ${hours}h ${minutes}m ${seconds}s`);
      } else {
        setTimeToListing("LISTED!");
      }
    }, 1000);

    setListingDate(LISTING_DATE.toLocaleDateString());
    return () => clearInterval(timer);
  }, []);

  const startMining = () => {
    setIsMining(true);
    hashCountRef.current = 0;

    // Hash rate counter
    hashRateIntervalRef.current = setInterval(() => {
      setHashRate(hashCountRef.current);
      hashCountRef.current = 0;
    }, 1000);

    // Mining loop
    miningIntervalRef.current = setInterval(() => {
      const nonce = Math.random().toString(36).substring(7);
      const timestamp = Date.now().toString();
      const data = `TriCoin-${timestamp}-${nonce}-${totalHashes}`;
      
      const hash = ts1613Hash(data);
      setCurrentHash(hash);
      
      // Check if hash meets difficulty (starts with required number of zeros)
      const target = "0".repeat(difficulty);
      if (hash.startsWith(target)) {
        setMinedCoins(prev => prev + 1);
        // Increase difficulty slightly after finding a block
        if (Math.random() < 0.1) {
          setDifficulty(prev => Math.min(prev + 1, 8));
        }
      }
      
      setTotalHashes(prev => prev + 1);
      hashCountRef.current++;
    }, 10);
  };

  const stopMining = () => {
    setIsMining(false);
    if (miningIntervalRef.current) {
      clearInterval(miningIntervalRef.current);
    }
    if (hashRateIntervalRef.current) {
      clearInterval(hashRateIntervalRef.current);
    }
    setHashRate(0);
  };

  return (
    <div className="min-h-screen bg-black text-cyan-400" style={{
      backgroundImage: "radial-gradient(circle at center, #001a33 0%, #000 70%)"
    }}>
      <Header />
      
      <main className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto">
          {/* Header */}
          <div className="text-center mb-8">
            <h1 className="text-4xl md:text-6xl font-bold mb-4" style={{
              fontFamily: "Orbitron, monospace",
              textShadow: "0 0 10px #0ff",
              letterSpacing: "2px"
            }}>
              <span className="text-cyan-400">TRICOIN</span>{" "}
              <span className="text-blue-400">MINER v1.0</span>
            </h1>
            <p className="text-xl text-cyan-300">
              Mine TriCoin using the TS1613 Hash Algorithm
            </p>
          </div>

          {/* Countdown Timer */}
          <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-6 mb-8 text-center">
            <h2 className="text-2xl font-bold mb-2 text-cyan-300">Time to Listing</h2>
            <div className="text-4xl font-mono font-bold text-cyan-400 mb-2">
              {timeToListing}
            </div>
            <p className="text-cyan-300">Listing Date: {listingDate}</p>
          </div>

          {/* Mining Stats */}
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-4 mb-8">
            <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-cyan-400">{minedCoins}</div>
              <div className="text-cyan-300">TriCoins Mined</div>
            </div>
            <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-cyan-400">{hashRate}</div>
              <div className="text-cyan-300">Hashes/sec</div>
            </div>
            <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-cyan-400">{totalHashes}</div>
              <div className="text-cyan-300">Total Hashes</div>
            </div>
            <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-4 text-center">
              <div className="text-2xl font-bold text-cyan-400">{difficulty}</div>
              <div className="text-cyan-300">Difficulty</div>
            </div>
          </div>

          {/* Mining Controls */}
          <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-6 mb-8">
            <div className="text-center mb-4">
              <button
                onClick={isMining ? stopMining : startMining}
                className={`px-8 py-4 rounded-lg font-bold text-lg uppercase tracking-wider transition-all duration-300 ${
                  isMining
                    ? "bg-red-600 hover:bg-red-700 text-white"
                    : "bg-gradient-to-r from-blue-600 to-cyan-500 hover:from-cyan-500 hover:to-blue-600 text-black"
                }`}
                style={{
                  boxShadow: isMining ? "0 0 10px #ff0000" : "0 0 10px #0ff"
                }}
              >
                {isMining ? "Stop Mining" : "Start Mining"}
              </button>
            </div>
            
            <div className="text-center">
              <div className={`inline-block px-4 py-2 rounded-full ${
                isMining ? "bg-green-600" : "bg-gray-600"
              }`}>
              Status: {isMining ? "MINING" : "IDLE"}
              </div>
            </div>
          </div>

          {/* Current Hash Display */}
          {currentHash && (
            <div className="bg-cyan-900/20 border border-cyan-400 rounded-lg p-6">
              <h3 className="text-xl font-bold mb-4 text-cyan-300">Latest Hash:</h3>
              <div className="bg-black/50 p-4 rounded border border-cyan-600 font-mono text-sm break-all text-cyan-400">
                {currentHash}
              </div>
              <div className="mt-2 text-cyan-300">
                Length: {currentHash.length} characters
              </div>
            </div>
          )}

          {/* Algorithm Info */}
          <div className="mt-8 bg-cyan-900/20 border border-cyan-400 rounded-lg p-6">
            <h3 className="text-xl font-bold mb-4 text-cyan-300">TS1613 Algorithm Info:</h3>
            <ul className="space-y-2 text-cyan-300">
              <li>• Uses Golden Ratio (φ = 1.618...) for mathematical operations</li>
              <li>• 3-phase hashing with 40 mixing rounds</li>
              <li>• 1613-character output length</li>
              <li>• Custom salt generation with 153-bit entropy</li>
              <li>• 1000 iterations for enhanced security</li>
            </ul>
          </div>
        </div>
      </main>
    </div>
  );
}
